import { Injectable, Inject } from '@angular/core';
import { Http, Headers, Response } from '@angular/http';

import 'rxjs/add/operator/toPromise';
import { Auth } from '../domain/auth';
import { User } from '../domain/user';

@Injectable()
export class AuthService {

  constructor(private http: Http, @Inject('user') private userService) { }

  loginWithCredentials(us:User): Promise<Auth> {
    return this.userService.getUserByUsername(us.username)  //username//UserServiceuser
      .then(user => {
        let auth = new Auth();
        localStorage.removeItem('userId');  //userId




        //
        //auth-guard 
        // 


        let redirectUrl = (localStorage.getItem('redirectUrl') === null) ?
          '' : localStorage.getItem('redirectUrl');
        auth.redirectUrl = redirectUrl;      //Url
        if (null === user) {
          //user
          auth.hasError = true;
          auth.errMsg = 'user not found';

        } else if (us.password === user.password) {
          //user
          auth.user = Object.assign({}, user);
          auth.hasError = false;
          localStorage.setItem('userId', user.id);
          localStorage.setItem('belongclass',user.belongclass);
        } else {
          //
          // 
          auth.hasError = true;
          auth.errMsg = 'password not match';
        }
        console.log(auth);
        return auth;
      })
      .catch(this.handleError);
  }
  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error);
    return Promise.reject(error.message || error);
  }
}